/* base styles */

* {
    margin: 0;
    font-family: Arial, Helvetica, sans-serif;
    color: #333;
    background-color: rgb(225, 204, 226);
}

.navbar {
    padding: 20px;
    display: flex;
    align-items: center;
    max-width: 600px;
    margin: 0 auto;
    border-bottom: 1px solid #f2f2f2;
}

.navbar h1 {
    color: #f1356d;
}

.navbar .links {
    margin-left: auto;
}

.navbar a {
    margin-left: 16px;
    text-decoration: none;
    padding: 6px;
}

.navbar a:hover {
    color: #f1356d;
}

.content {
    max-width: 600px;
    margin: 40px auto;
    padding: 20px;
}


/* blog previews / list */

.blog-preview {
    padding: 10px 16px;
    margin: 20px 0;
    border-bottom: 1px solid #fafafa;
}

.blog-preview:hover {
    box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.1);
}

.blog-preview h2 {
    font-size: 20px;
    color: #f1356d;
    margin-bottom: 8px;
}

.blog-preview a {
    text-decoration: none;
}


/* blog-details */

.blog-details h2 {
    font-size: 20px;
    color: #f1356d;
    margin-top: 10px;
}

.blog-details div {
    margin: 20px 0px;
}

.blog-details button {
    background: #f1356d;
    color: #f2f2f2;
    border: 0;
    padding: 8px;
    border-radius: 8px;
    cursor: pointer;
}


/*  create new blog form*/

.create {
    max-width: 400px;
    margin: 0 auto;
    text-align: center;
}

.create label {
    text-align: left;
    display: block;
    font-size: 20px;
    color: #2c1a20;
}

.create h2 {
    font-size: 20px;
    color: #f1356d;
    margin-bottom: 30px;
    font-family: Arial, Helvetica, sans-serif;
}

.create input,
.create textarea,
.create select {
    width: 100%;
    padding: 10px 10px;
    margin: 10px 0;
    border: 1px solid #333;
    box-sizing: border-box;
    display: block;
    border-radius: 10px;
}

.create button {
    background: #f1356d;
    color: #f2f2f2;
    border: 0;
    margin-top: 30px;
    padding: 10px 20px;
    border-radius: 8px;
    cursor: pointer;
}

.not-found {
    max-width: 400px;
    margin: 0 auto;
    text-align: center;
}

.not-found h2 {
    margin: 40px 30px;
}

.not-found a {
    background-color: #f1356d;
    text-decoration: none;
    padding: 10px;
    margin: 0 auto;
    border-radius: 10px;
}